.titileDoc h4{
	color : #4050f9;
	padding: 20px 0px 2px 15px;
}

.left-panel{
	height: 100vh;
}

.titileDoc{
	background: #fff;
	height: calc(100vh - 200px);
}

.outer-circle-upload-circle{
	width: 140px;
    height: 140px;
    border-radius: 100px;
    text-align: center;
    background: #fff;
    position: relative;
    margin: 35px auto;
}

.inner-circle-upload-circle{
	position: absolute;
  	top: 50%;
  	transform: translateY(-50%);
  	margin: 0 auto;
  	text-align: center;
  	width: 100%;
}

.upload-percent{
	font-size: 30px;
}

.total-upload{
	color: #a9a9a9;
}

.tab-item{
	position: relative;
	padding: 15px 15px;
	cursor: pointer;
}

.tab-item.active{
	border-left: 2px solid #4050f9;
}

.tab-name{
	color: #3f495b;
	padding-left: 12px;
	cursor: pointer;
}

.tab-content{
	display: none;
}

.tab-active{
	display: block;
}

.notify{
	width: 45px;
    text-align: center;
    border-radius: 20px;
    background: #f0f0f7;
    color: #b7b7c8;
    padding: 2px;
    position: absolute;
    right: 15px;
    top: 12px;
}

.tab-item.active .notify{
	color: #4050f9;
	background: #dee0ff;
}


.tab-item.active .tab-name, .signup-txt{
	color: #4050f9;
}

.upload-verify-txt{
	font-size: 14px;
    color: #676767;
}

.tab-icon{
	color: #9a9898;
}

.lock-btn{
	text-align: center;
    background: #fff;
    padding: 10px;
    border: 1px solid #f0eff4;
    border-radius: 4px;
}

.signup-block{
	width: 200px;
    background: #fff;
    padding: 15px;
    font-size: 12px;
    color: #a4a6b9;
    border-radius: 4px;
    display: inline-block;
    margin-right: 20px;
}

.lady-iamge{
	display: inline-block;
}

.get-access-block{
	padding-top: 10px;
}

.get-access-block .access-txt{
	font-size: 14px;
    color: #9e9c9c;
    padding-top: 5px;
    display: inline-block;
}

.quote-block{
	color: #3e3e4d;
    font-size: 20px;
    margin: 0 auto;
    width: 68%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 15px;
}

.quote-msg .author-name{
	font-size: 14px;
    color: #888888;
}

.quote-symbol{
	position: absolute;
    left: -12px;
    top: -24px;
}

.right-header{
	color: #595c60;
    font-size: 17px;
    clear: both;
    overflow: hidden;
    padding-top: 22px;
}

.input-hidden{
    position: absolute;
    opacity: 0;
    width: 202px;
    z-index: 44444;
    cursor: pointer;
    height: 36px;
}


.uploading-text{
	font-size: 22px;
    color: #202020;
    font-weight: 500;
}


.logout-btn{
	border: 1px solid #e2e2e2;
    padding: 5px 20px;
    border-radius: 4px;
    color: #828080;
    font-weight: 300;
    background: #fff;
    outline: none !important;
    box-shadow: none;
}

.personal-information-detail{
	position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 auto;
    text-align: center;
    width: 100%;
}


.input-box-group .first-name{
	padding-right: 25px;
}

.input-box-group{
	padding-top: 30px;
}


.upload-btn{
	padding-top: 58px;
}

.upload-btn .MuiButton-containedPrimary{
	background-color : #4050f9 !important;
}

.upload-verify{
	display: inline-block;
    padding-top: 6px;
}


.input-box-group .MuiFormLabel-root{
	color: #aeafc0 !important;
	font-size: 15px;
}

.input-box-group .MuiInputBase-input{
	font-size: 18px;
}

.personal-info-footer, .prev-confirm-group, .signup-container{
	position: absolute;
    bottom: 15px;
    width: 100%;
}

.img-correct-msg{
	width: 250px;
    color: #696767;
    font-size: 15px;
}

.save-continue{
	text-align: center;
    margin: 0 auto;
    color: #4050f9;
    font-size: 14px;
    height: 25px;
}

.aadhar-img{
	position: absolute;
    right: -150px;
    bottom: -8px;
}

.arrow-img-block{
	position: absolute;
    right: 158px;
    bottom: 90px;
}

.prev-confirm-group{
	font-size: 14px;
}

.prev-confirm-group .enter-btn{
	border: 1px solid #e2e2e2;
    padding: 3px 10px;
    border-radius: 2px;
    color: #828080;
}


.prev-confirm-group span:first-child{
	padding-right: 18px;
}

.signup-container{
	right: 0;
    width: auto;
    bottom: 0;
}

.upload-file-size{
	color: #959696;
}

.upload-file-name-block, .upload-file-type{
	display: inline-block;
}

.upload-file-type img{
	/*vertical-align: bottom;*/
}

.upload-file-name, .file-size-point{
	font-family: cursive !important;
}

.upload-file-name-block{
	vertical-align: middle;
	padding-left: 20px;
}

.upload-file-block{
	padding-top: 18px;
}

.sucess-msg{
	color: #2d732a;;
}

.upload-error-model .doc-error{
	font-size: 20px;
	color: #e30000;
	padding: 8px 0px;
	font-weight: 500;
}

.upload-error-model .modal-body{
	padding: 45px;
}

.upload-error-model .modal-dialog{
	max-width: 600px;
}

.ops-text{
	color: #3e3e4d;
    font-size: 17px;
    color: #63636f;
}

.img-error{
	color: #888888;
    font-size: 14px;
}

.upload-error-model .upload-btn{
	padding-top: 25px;
}

.document-image{
	padding-top: 10px;
}

.modelCloseIcon{
	position: absolute;
    top: -14px;
    left: -36px;
    padding: 11px 10px;
    border-radius: 30px;
    background: #7d7d7d;
    cursor: pointer;
}

.modelCloseIcon span{
	border-radius: 42px;
    background: #fff;
    padding: 5px 9px;
}

.modelCloseIcon svg{
	width: 11px;
	height: 11px;
}

.ach-info .upload-btn{
    padding-top: 20px;
}


.achError .img-correct-msg{
	width: auto;
}


