table, caption, tbody, tfoot, thead, tr, th, td {
	border: 0 !important;
}

tr th {
	border-bottom: 1.5px dashed lightgrey !important;
}

tr td {
	border-bottom: none
}

.table th, .table td {
	padding-top: 18px;
	vertical-align: middle !important;
}

tr {
	border-radius: 13px;
	font-size: 15px;
}

tbody:before {
	content: "-";
	display: block;
	line-height: 1.5em;
	color: transparent;
}

table tr:hover td:first-child {
	border-top-left-radius: 13px;
	border-bottom-left-radius: 13px;
}

table tr:hover td:last-child {
	border-top-right-radius: 13px;
	border-bottom-right-radius: 13px;
}

/* navigation bar */

.navbar {
	background-color: white;
	margin-bottom: 40px;
	box-shadow: 0 2px 4px rgba(201, 199, 199, 0.5);
}

.navbar-brandAdd {
	color: #2585fe !important;
	font-family: Helvetica !important;
	font-size: 11px !important;
	letter-spacing: 3.31px;
	text-transform: uppercase;
}

.navbar-light .navbar-nav .nav-link {
	position: relative;
	padding: 6px;
}

.navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .dropdown-item:hover {
	color: #2585fe;
	font-family: Helvetica
}

.navbar-light .navbar-nav .nav-link:hover::after {
	content: '';
	display: block;
	width: 100%;
	height: 3px;
	background: #2C89FE;
	animation: menu-highlight 0.2s;
	position: absolute;
	bottom: -13px;
	left: 0;
	right: 0;
}

.navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .dropdown-item.active {
	color: #2585fe !important;
	font-weight: 700;
	font-family: Helvetica !important;
}

.navbar-light .navbar-nav .nav-link.active::after {
	content: '';
	display: block;
	width: 100%;
	height: 3px;
	background: #2C89FE;
	position: absolute;
	bottom: -13px;
	left: 0;
	right: 0;
}

.singleApp:last-child .node-wrapper .show, .singleApp .beforeLastApp .show{
	top:-84px !important;
}

.config-input-box .form-check-label{
	color: #383737;
}
.custom-input input[type='radio']:checked:after, .custom-input input[type='radio']:after{
	width: 16px;
    height: 16px;
    top:0px;
}

.custom-dropdown, .dropdown-heading{
	background-image: url(../src/assets/drop-down-arrow.png);
    background-size: 14px;
    background-repeat: no-repeat;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-border-radius: 0px;
    background-position: 95% 50%;
    cursor: pointer !important;
}


@keyframes menu-highlight {
	from {
		width: 0;
	}
	to {
		width: 100%;
	}
}

